import React, { useEffect, useState } from "react";

import {
  Row,
  Col,
  Input,
  Card,
  Pagination,
  Popconfirm,
  Spin,
  Button,
} from "antd";
import { Table, Space } from "antd";
import { RiErrorWarningLine,  } from "react-icons/ri";
import PageTitle from "../../../../layout/components/content/page-title";
import { Search } from "react-iconly";
import { connect } from "react-redux";
import { FilterOutlined, UploadOutlined, PlusOutlined  } from "@ant-design/icons";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import moment from "moment";
import {
  getEnquirys,
  enquiryDelete,
  isModelVisible,
  SetEnquiryParams,
  isDrawerVisible,
  getEnquiryByID,
  enquiryViewModel,
  clearEnquiryList,
} from "../../../../store/sideMenu/Enquiry/enquirySlice";
import { isMobile } from "react-device-detect";

import {
  getMiniVersion,
  getMiniBrands,
  getMiniBrandFamily,
  getMiniModels,
} from "../../../../store/mini/miniSlice";
import AddEnquiry from "./addEnquiry";
import Filter from "./Filters/Filter";
import EnquiryView from "./enquiryView";
import { getReportList } from "../../../../store/reports/reportsSlice";


function Enquiry(props) {
  const { Search } = Input;

  useEffect(() => {
    props.getEnquirys({ page: 1, search: "", page_size: 10 });
    props.clearEnquiryList();
  }, []);

  const columnClass =
    "hp-p1-body hp-text-color-black-50 hp-text-color-dark-0 hp-font-weight-500 ";

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: 70,
      fixed: isMobile ? null : "left",
    },
    {
      title: "Code",
      width: 110,
      fixed: isMobile ? null : "left",
      render: (row) => <span className={columnClass}>{row.code}</span>,
    },
    {
      title: "Date",
      width: 110,
      render: (row) => <span className={columnClass}>{moment(row.date).format('DD-MM-YYYY')}</span>,
    },
    {
      title: "Enquiry Type",
      filterMode: "tree",
      filterSearch: true,
      render: (row) => (
        <span className={columnClass}>{row.enquirytype_name}</span>
      ),
      width: 120,
    },
    {
      title: "Customer Name",
      filterMode: "tree",
      filterSearch: true,
      render: (row) => <span className={columnClass}>{row.customername}</span>,
      width: 150,
    },
    {
      title: "Customer Mobile",
      filterMode: "tree",
      filterSearch: true,
      render: (row) => (
        <span className={columnClass}> {row.customermobile}</span>
      ),
      width: 120,
    },
    {
      title: "Counter",
      width: 120,
      filterMode: "tree",
      filterSearch: true,
      render: (row) => <span className={columnClass}>{row.counter?.name}</span>,
    },
    {
      title: "Occupation",
      filterMode: "tree",
      filterSearch: true,
      render: (row) => (
        <span className={columnClass}>{row.occupation?.name}</span>
      ),
      width: 120,
    },

    {
      title: "Vehicle Number",
      // sorter: (a, b) => a.vehiclenumber.length - b.vehiclenumber.length,
      filterMode: "tree",
      filterSearch: true,
      render: (row) => <span className={columnClass}>
        {row.enquirytype == 1 || row.enquirytype == 4 ?  (
          row.vehiclenumber
        ) : (
          row.enquirytype == 2 || row.enquirytype == 3 ? (
            row.vehicle?.vehicleno
          ) : (
            ""
          )
        )}</span>,
      width: 130,
    },
    {
      title: "Version",
      // sorter: (a, b) => a.version.name.length - b.version.name.length,
      filterMode: "tree",
      filterSearch: true,
      render: (row) => <span className={columnClass}>{row.version?.name}</span>,
      width: 150,
    },
    {
      title: "Color",

      filterMode: "tree",
      filterSearch: true,
      render: (row) => <span className={columnClass}>{row.color}</span>,
      width: 100,
    },
    {
      title: "Vehicle Reading",
      filterMode: "tree",
      filterSearch: true,
      render: (row) => (
        <span className={columnClass}>{row.vehiclereading}</span>
      ),
      width: 120,
    },
    {
      title: "Planned Days",
      filterMode: "tree",
      filterSearch: true,
      render: (row) => <span className={columnClass}>{row.planneddays}</span>,
      width: 100,
    },
    {
      title: "Year",

      filterMode: "tree",
      filterSearch: true,
      render: (row) => <span className={columnClass}>{row.year}</span>,
      width: 100,
    },
    {
      title: "Required Amount",

      filterMode: "tree",
      filterSearch: true,
      render: (row) => (
        <span className={columnClass}>{row.requiredamount}</span>
      ),
      width: 120,
    },
    {
      title: "Budget Amount",
      filterMode: "tree",
      filterSearch: true,
      render: (row) => <span className={columnClass}>{row.budget}</span>,
      width: 120,
    },
    {
      title: "Description",
      sorter: (a, b) => a.name.length - b.name.length,
      filterMode: "tree",
      filterSearch: true,
      render: (row) => <span className={columnClass}>{row.description}</span>,
      width: 150,
    },
    {
      title: "Actions",
      key: "action",
      width: 100,
      fixed: isMobile ? null : "right",
      render: (row, record) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Purchase.change_enquiry") !== -1 && (
            <i
              className="ri-edit-box-line hp-mr-2 "
              style={{ cursor: "pointer", color: "#fc6f03" }}
              onClick={() => {
                props.getEnquiryByID({row, type: 'update'});
                props.getMiniBrands();
                props.getMiniBrandFamily({ brand: row.version?.brand?.id });
                props.getMiniModels({ brandfamily: row.version?.brandfamily?.id });          
                props.getMiniVersion({ model: row.version?.model?.id });
              }}
            ></i>
          )}
          {props.userAccessList?.indexOf("Purchase.delete_enquiry") !== -1 && (
            <Popconfirm
              placement="topRight"
              title="Are you sure to delete this Record?"
              onConfirm={() => props.enquiryDelete(record.id)}
              okText="Yes"
              cancelText="No"
              className="hp-ml-8"
              icon={
                <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
              }
            >
              <i
                className="ri-delete-bin-2-line "
                style={{ cursor: "pointer", color: "#ff0000" }}
              ></i>
            </Popconfirm>
          )}
          {props.userAccessList?.indexOf("Purchase.view_enquiry") !== -1 && (
            <i
              className="ri-eye-line hp-mr-2 "
              style={{ cursor: "pointer", color: "#fc6f03" }}
              onClick={() => {
                props.getEnquiryByID({row, type: 'view'});
              }}
            ></i>
          )}
        </Space>
      ),
    },
  ];

  const data = props.enquirylist.map((row, key) => ({
    ...row,
    index:
      (props.enquiryParams.page - 1) * props.enquiryParams.page_size + key + 1,
  }));

  function onPageChange(page, pageSize) {
    props.clearEnquiryList();
    props.SetEnquiryParams({
      ...props.enquiryParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getEnquirys({
      ...props.enquiryParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetEnquiryParams({ ...props.enquiryParams, search: val, page: 1 });
    props.getEnquirys({ ...props.enquiryParams, search: val, page: 1 });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  

  function exportData() {
    // var { file_format, ...params } = props.reportParamsData;
    var objData = {
      file_format: 0,
      model_name: "Enquiry",
    };

    props.getReportList({
      objData,
      ParamsData: {
        ...props.enquiryParams,
        start_date:
          props.enquiryParams.start_date != null &&
          props.enquiryParams.start_date != ""
            ? moment(props.enquiryParams.start_date).format("YYYY-MM-DD")
            : "",
        end_date:
          props.enquiryParams.end_date != null &&
          props.enquiryParams.end_date != ""
            ? moment(props.enquiryParams.end_date)
                .add(1, "days")
                .format("YYYY-MM-DD")
            : "",
      },
    });
  }

  return (
    <Row gutter={[12, 12]} >
      <PageTitle
        pageTitle="Enquiry"
      />

      <Col span={24}>
        <Row gutter={[12, 12]} className="hp-ecommerce-app-header ">
          <Col span={24} xs={24} sm={24} md={14} className="hp-ecommerce-app-header-search">
            <Search
              placeholder="Search..."
              allowClear
              onChange={(e) => {
                if (e.target.value == "") {
                  props.getEnquirys({
                    ...props.enquiryParams,
                    page: 1,
                    search: "",
                  });
                  props.SetEnquiryParams({
                    ...props.enquiryParams,
                    page: 1,
                    search: "",
                  });
                }
              }}
              onSearch={(value) => {
                onSearch(value);
              }}
            />
          </Col>

          <Col span={24} xs={8} sm={8} md={3}>
            <Button
              onClick={() => {
                props.isDrawerVisible(true);
              }}
              // className="hp-w-100 hp-p-12"
              className={
                props.isFilterActive ? "hp-bg-primary-1 hp-w-100 hp-p-12 hp-text-color-white hp-font-weight-600" : "hp-w-100"
              }
              icon={<FilterOutlined className="hp-mr-10" />}
            >
              {!isMobile && "Filter"}
            </Button>
          </Col>

          {props.userAccessList?.indexOf("Purchase.add_enquiry") !== -1 && (
            <Col span={24} xs={8} sm={8} md={4}>
              <Button
                onClick={() => {
                  props.isModelVisible(true);
                }}
                className="hp-w-100 hp-p-12"
                icon={<PlusOutlined className="hp-mr-10" />}
              >
                {!isMobile && "Add Enquiry"}
              </Button>
            </Col>
          )}

          {props.userAccessList?.indexOf("Purchase.export_enquiry") !== -1 && (
            <Col span={24} xs={8} sm={8} md={3}>
              <Popconfirm
                placement="topRight"
                title="Are you sure to export this Record?"
                onConfirm={() => exportData()}
                okText="Yes"
                cancelText="No"
                className="hp-ml-8"
                icon={
                  <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
                }
              >
                <Button
                  className="hp-w-100 hp-p-12"
                  icon={<UploadOutlined className="hp-mr-10" />}
                >
                  {!isMobile && "Export"}
                </Button>
              </Popconfirm>
            </Col>
          )}
        </Row>
      </Col>

      <Col span={24}>
      <Spin tip="Loading..." spinning={props.loading} delay={500}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={24}>
              {/* <Spin tip="Loading..." spinning={props.loading} delay={500}> */}
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 1300 }}
                  pagination={false}
                  size="middle"
                />
             
            </Col>
            <Col span={24}>
              {!props.loading && (
              <Pagination
                size="small"
                current={props.enquiryParams?.page}
                pageSize={props.enquiryParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
              ) }
            </Col>
          </Row>
        </Card>
        </Spin>
      </Col>
      {props.model && <AddEnquiry />}
        <Filter />
      {props.viewModel && <EnquiryView />}
    </Row>
  );
}

function mapStateToProps({ enquiry, system }) {
  return {
    enquirylist: enquiry.enquirylist,
    noofpages: enquiry.noofpages,
    listCount: enquiry.listCount,
    enquiryParams: enquiry.enquiryParams,
    loading: enquiry.loading,
    userAccessList: system.userAccessList,
    isFilterActive: enquiry.isFilterActive,
    viewModel: enquiry.viewModel,
    model: enquiry.model,
  };
}

export default connect(mapStateToProps, {
  getEnquirys,
  enquiryDelete,
  isModelVisible,
  SetEnquiryParams,
  getMiniVersion,
  getMiniBrands,
  getMiniBrandFamily,
  getMiniModels,
  isDrawerVisible,
  getEnquiryByID,
  enquiryViewModel,
  getReportList,
  clearEnquiryList,
})(Enquiry);
