import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  Row,
  Col,
  Divider,
  Form,
  Input,
  Button,
  Drawer,
  Space,
  Select,
  DatePicker,
} from "antd";
import moment, { min } from "moment";

import {
  isDrawerVisible,
  FilterInputChangeValue,
  getEnquirys,
  SetEnquiryParams,
} from "../../../../../store/sideMenu/Enquiry/enquirySlice";
import {
  getMiniCounters,
  getMiniOccupation,
  getMiniVersion,
} from "../../../../../store/mini/miniSlice";

var date = new Date(),
  y = date.getFullYear(),
  m = date.getMonth();

const filter = (props) => {
  const [form] = Form.useForm();
  useEffect(() => {
    props.getMiniOccupation();
    props.getMiniCounters();
    props.getMiniVersion();
  }, []);
  const enquiryTypeList = [
    { id: 1, name: "Purchase" },
    { id: 2, name: "Direct Sale" },
    { id: 3, name: "Finance Sale" },
    { id: 4, name: "OVF Sale" },
  ];



  const onFinish = () => {
    props.SetEnquiryParams({
      ...props.enquiryParams,
      start_date: props.enquiryParams.start_date,
      end_date: props.enquiryParams.end_date,
      page: 1,
      search: "",
      page_size: 10,
    });

    props.getEnquirys({
      ...props.enquiryParams,
      // start_date:
      //   props.enquiryParams.start_date != null &&
      //   props.enquiryParams.start_date != ""
      //     ? moment(props.enquiryParams.start_date).format("YYYY-MM-DD")
      //     : "",
      // end_date:
      //   props.enquiryParams.end_date != null && props.enquiryParams.end_date != ""
      //     ? moment(props.enquiryParams.end_date)
      //         .add(1, "days")
      //         .format("YYYY-MM-DD")
      //     : "",
      start_date: props.enquiryParams.start_date,
      end_date: props.enquiryParams.end_date,
      page: 1,
      search: "",
      page_size: 10,
    });
    props.isDrawerVisible(false);
  };

  function clearFilter() {
    props.SetEnquiryParams({
      start_date: "",
      end_date: "",
      page: 1,
      search: "",
      page_size: 10,
    });
    props.getEnquirys({
      start_date: "",
      end_date: "",
      page: 1,
      search: "",
      page_size: 10,
    });
    props.getMiniOccupation();
  }

  function disableDateRanges(range = { startDate: false, endDate: false }) {
    const { startDate, endDate } = range;
    return function disabledDate(current) {
      let startCheck = true;
      let endCheck = true;
      if (startDate) {
        startCheck =
          current &&
          current < moment(startDate + " 00:00:01", "YYYY-MM-DD HH:mm:ss");
      }
      if (endDate) {
        endCheck =
          current &&
          current >
            moment(
              moment(endDate).format("YYYY-MM-DD") + " 23:59:59",
              "YYYY-MM-DD HH:mm:ss"
            );
      }

      return (startDate && startCheck) || (endDate && endCheck);
    };
  }

  useEffect(() => {
    form.setFieldsValue({
      start_date: props.enquiryParams?.start_date,
      end_date: props.enquiryParams?.end_date,
      occupation: props.enquiryParams?.occupation,
      enquirytype: props.enquiryParams?.enquirytype,
      version: props.enquiryParams?.version,
      counter: props.enquiryParams?.counter,
    });
  }, [props.enquiryParams]);

  function onSearch(val) {
    props.getEnquirys({ page: 1, search: val, page_size: 10 });
  }


  return (
    <Row gutter={[24, 24]}>
      <Col span={24}>
        <Drawer
          title="Filters"
          width={420}
          className="hp-drawer-mobile"
          onClose={() => {
            props.isDrawerVisible(false);
          }}
          placement="right"
          visible={props.drawer}
        >
          <Form
            form={form}
            layout="vertical"
            name="basic"
            initialValues={{ remember: true }}
            onFinish={onFinish}
          >
            <Row gutter={[12, 12]}>
              <Col span={12}>
                <Form.Item label="From Date" name="start_date" className="hp-mb-10">
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: new Date(),
                    })}
                    onChange={(date) => {
                      props.FilterInputChangeValue({
                        key: "start_date",
                        value: date == null ? "" : date,
                      });
                      if (date == null) {
                        props.FilterInputChangeValue({
                          key: "end_date",
                          value: "",
                        });
                      }
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="To Date" name="end_date" className="hp-mb-10">
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: moment().format("YYYY-MM-DD"),
                      startDate: moment(props.enquiryParams?.start_date).format(
                        "YYYY-MM-DD"
                      ),
                    })}
                    disabled={
                      props.enquiryParams.start_date == null ||
                      props.enquiryParams.start_date == ""
                        ? true
                        : false
                    }
                    onChange={(date) => {
                      props.FilterInputChangeValue({
                        key: "end_date",
                        value: date == null ? "" : date,
                      });
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="Enquiry Type" name="enquirytype" className="hp-mb-10">
                  <Select
                    showSearch
                    allowClear
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    placeholder="Select Enquiry Type"
                    optionFilterProp="enquirytype"
                    onChange={(value) => {
                      props.FilterInputChangeValue({
                        key: "enquirytype",
                        value: value,
                      });
                    }}
                    value={
                      props.enquiryParams.enquirytype == null
                        ? ""
                        : props.enquiryParams?.enquirytype
                    }
                  >
                    {enquiryTypeList.map((enquirytype, index) => {
                      return (
                        <Option key={index} value={enquirytype.id}>
                          {enquirytype.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="Occupation :" name="occupation" className="hp-mb-10">
                  <Select
                    showSearch
                    allowClear
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    placeholder="Select Occupation"
                    optionFilterProp="occupation"
                    onChange={(value) => {
                      props.FilterInputChangeValue({
                        key: "occupation",
                        value: value,
                      });
                    }}
                    value={
                      props.enquiryParams.occupation_id == null
                        ? ""
                        : props.enquiryParams?.occupation_id
                    }
                  >
                    {props.miniOccupationList.map((occupation, index) => {
                      return (
                        <Option key={index} value={occupation.id}>
                          {occupation.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={12} className="hp-pr-sm-0 hp-pr-12">
                <Form.Item label="Counter :" name="counter" >
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select a Counter"
                    optionFilterProp="children"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.FilterInputChangeValue({
                        key: "counter",
                        value: value,
                      });
                    }}
                    value={
                      props.enquiryParams.counter_id == null
                        ? ""
                        : props.enquiryParams?.counter_id
                    }
                  >
                    {props.miniCountersList.map((bank, index) => {
                      return (
                        <Option key={index} value={bank.id}>
                          {bank.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={12} className="hp-pr-sm-0 hp-pr-12">
                <Form.Item label="Version :" name="version">
                  <Select
                    showSearch
                    allowClear
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    placeholder="Select Version"
                    optionFilterProp="Version"
                    onChange={(value) => {
                      props.FilterInputChangeValue({
                        key: "version",
                        value: value,
                      });
                    }}
                    value={
                      props.enquiryParams.version_id == null
                        ? ""
                        : props.enquiryParams?.version_id
                    }
                  >
                    {props.miniVersionList.map((version, index) => {
                      return (
                        <Option key={index} value={version.id}>
                          {version.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={[12, 12]}>
              <Col md={12}>
                <Button
                  block
                  type="secondary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    clearFilter();
                  }}
                  htmlType="button"
                >
                  Clear
                </Button>
              </Col>
              <Col md={12}>
                <Button
                  block
                  type="primary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    form.submit();
                  }}
                >
                  Apply
                </Button>
              </Col>
            </Row>
          </Form>
        </Drawer>
      </Col>
    </Row>
  );
};

function mapStateToProps({ enquiry, mini }) {
  return {
    drawer: enquiry.drawer,
    enquiryParams: enquiry.enquiryParams,
    miniCountersList: mini.miniCountersList,
    miniOccupationList: mini.miniOccupationList,
    miniVersionList: mini.miniVersionList,
  };
}

export default connect(mapStateToProps, {
  isDrawerVisible,
  SetEnquiryParams,
  FilterInputChangeValue,
  getEnquirys,
  getMiniCounters,
  getMiniOccupation,
  getMiniVersion,
})(filter);
